<!-- 这个组件点击后要跳转到 articleView 去。
     并且要携带文章标识参数 
    这个组件的条目是根据接收的自动数据生成的。 -->
<!-- 这个组件不再使用，但是因为在分页功能的实现上用了较多时间，所以保留做纪念-->
<template>
  <div class="articleList">
    <!-- 轮播 -->
    <!-- 见 lunbo.vue 组件，轮播功能不再使用。 -->

    <!-- 文章 -->
    <!-- 一个 articleList ：根据页码展示全部数据的 8项。 -->
    <!--  -->
    <div v-for="article in temp" :key="article.id">
      <Article v-if="length > 0" :article="article"></Article>
    </div>

  <!-- 点击跳转页面后，要把数据也更上去。 -->
  <!-- articleInfo 与 page 的关系 ：
                  page为 1，则取数组前 10 项展示；
                  page 为 2，则取数组 10~20 项展示-->
    <!-- page：length/10 +1  -->
    <div style="margin-left: 30%; margin-top: 30px">
      <a-pagination simple :defaultPageSize="8" :default-current="1" :total="tempLength + 1" @change="onChange" />
    </div>


  </div>
</template>

<script>
import Article from "./article.vue";
export default {
  name: "articleListComponent",
  components: { Article },
  data() {
    return {
      length: "",//总的文章信息长度（这项已经没用了，仅仅用来测试数据流通情况）
      articleInfo: [],//存放总的文章信息
      cache:[],//存放分类后文章信息
      temp: [],//存放要展示的文章信息，大小为每个 page的项目数（8个）
      tempLength: "",//分类后的文章信息长度，或者总的文章信息长度
      page: "",
      flag: 0,//为 1时展示分类后的文章信息，默认为 0，展示所有文章信息。
   
    };
  },
  methods: {
    onChange(page) {
      //点击后跳转到顶部
      this.temp = [];
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0;

      // 如果是分类展示，则是根据择取后的articleInfo的信息截取
      if (this.flag == 1) {
        //this.temp = cate产生的新数组
        this.temp = this.cache.slice((page - 1) * 8, page * 8);
      }
      // 如果是非分类展示，则是根据articleInfo的总信息截取(默认)
      if (this.flag == 0) {
        //每个 page 展示 8 项数据
        this.temp = this.articleInfo.slice((page - 1) * 8, page * 8);
      }
      //测试
      console.log("Page: ", page);
    },
    cate(name) {
      if (name != '') {//表示有分类信息传入
         //点击后跳转到顶部
        document.documentElement.scrollTop = 0;
        document.body.scrollTop = 0;
        
        this.flag = 1;//表示有分类信息传入
        console.log(name);
        // 展示的temp换成一个新数组了
        this.cache = [];//存放分类后数据（长度不确定）
        for (var i = 0; i < this.articleInfo.length; i++) {
          if (this.articleInfo[i].category == name) {
            this.cache.push(this.articleInfo[i]); //分类后的文章信息存在cache中
          }
        }  
        this.temp = this.cache.slice(0, 8); //取前 8项展示
        //改变分页器的 total
        this.tempLength = this.temp.length;
        // 页码是改过来了，但是分类的数据仍然在一个页面上，超出了每页8项的限制。
        // v-for 展示 article 时，之前的 temp一直是八项，所以没问题，现在temp和articlelist一样，长度都是不确定的。
        console.log("cate事件回调成功执行了");
        
      }
      //问题 ： 分类组件的文章列表组件在同一个页面，改变temp值之后要再次回到原先所有文章的页面，
      // 只能刷新，或者切到别的页面后再切回来。
      // 解决：点击首页时刷新页面
    },
 
  },
  beforeUpdate() {
    //点击换页的时候，这里也会执行
    this.$bus.$on("cate", this.cate);
    // console.log("beforeUpdate，flag值为：",this.flag);//点击了分类，则 flag为 1。
  },
  mounted() {
    //mounted阶段，异步请求的数据尚未返回。
    console.log("mounted执行了,length值为：", this.length);//
  },
  created() {
    //切换组件后再次进入，created方法会再次调用
    //清空 temp 的值，因为没有展示分类数据的话就要展示所有数据
    //测试
    // console.log("hahah");
    console.log("created钩子");
    // this.$bus.$on("cate", this.cate);

    axios({
      method: "get",
      url: "http://localhost:3001/article/getall",
    })
      .then((res) => {

        this.length = res.data.data.length;
        for (var i = 0; i < this.length; i++) {
          this.articleInfo.unshift(res.data.data[i]); //倒序输出
        }
        console.log("created异步返回结果", this.length)
        this.tempLength = this.length;
        this.temp = this.articleInfo.slice(0, 8); //开局默认展示前8项，页码为 1 。
        // this.tempLength = this.length;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style scoped>
.articleList {
  /* position: relative; */
  /* background-color: #3c3c3c; */
  display: inline-block;
  vertical-align: top;
  /* background-color: rgb(229, 229, 225); */
  width: 70%;
  /* height:500px; */
  margin-left: 0%;
  padding-top:1%;
  /* margin-top:6% */
  /* 不设置高度，高度就自适应了 */
  /* max-height: 1400px; */
}
</style>