<!-- 这里由两部分div组成
    第一个将最新文章特写（包含封面图片）（ “修改日期” 这种鸡肋的东西还是不要了）
    第二个则是最近二十篇文章（只有文章题目，分类，发表日期） -->
<template>
    <div class="articleListNew">
        <!-- <a >{{ article.name }}</a> -->
        <Article :article="article"></Article>

        <!-- 以下这个组件可以拆分出去 -->
        <div class="latest">
            <div style="
                  display: inline-block;
                  background-color: #f5f5f5;
                  border-radius: 3px;
                  font-size: 20px;
                  margin: 15px 0 0 15px;
                  padding: 0 5px 0 5px;
                ">
                近期文章 :
            </div>

            <hr />

            <div v-for="article in temp" :key="article.id">
                <div id="latest_item" @click="jump(article.id, article.name)">
                    <div style="display: inline-block; width: ">
                        <a style="
                        font-size: 18px;
                        margin-left: 3px;
                        /* background-color: #e8e8e8; */
                        border-radius: 3px;
                        padding: 0 3px 0 3px;
                      ">{{ article.publish_time }}
                        </a>
                        >>
                        <a id="category" style="">{{ article.category }}</a>
                        >>
                    </div>

                    <div style="display: inline-block">
                        <!-- {{ article.category }} -->
                        <a id="articleName" style="
                              font-size: 18px;
                             margin-left: 3px;
                           /* background-color: #e8e8e8; */
                            border-radius: 3px;
                            padding: 0 3px 0 3px; ">{{ article.name }}</a>
                    </div>
                </div>
            </div>
            <hr />

            <div style="margin-left: 30%; margin-top: 30px">
                <a-pagination simple :defaultPageSize="10" :default-current="1" :total="articleInfo.length + 1"
                    @change="onChange" />
            </div>
            <br />
            <!-- <div id="moreArticle" style="">更多文章 . . . . . .</div> -->
        </div>
    </div>
</template>

<script>
import Article from "./article.vue";
import Pagination from "./pagination.vue";
export default {
    components: { Article, Pagination },
    name: "articleListNewComponent",
    //   props:["articleInfo"],
    data() {
        return {
            articleInfo: [], //存放所有文章
            temp: [], //存放最近的十篇文章
            article: {}, //最新的一篇文章，需要传给子组件，特别展示。
        };
    },
    methods: {
        jump(id, name) {
            // console.log(name);
            this.$router.push({
                name: "articleViewComponent",
                params: {
                    id,
                    name,
                },
            });
        },
        onChange(page) {
            this.temp = this.articleInfo.slice(1 + (page - 1) * 10, 1 + page * 10);
        }
    },
    created() {

        axios({
            method: "get",
            url: "http://43.139.56.159:3001/article/getall",
        })
            .then((res) => {
                for (var i = 0; i < res.data.data.length; i++) {
                    this.articleInfo.unshift(res.data.data[i]); //倒序输出
                }
                console.log(this.articleInfo);
                this.article = this.articleInfo[0];
                this.temp = this.articleInfo.slice(1, 11);
            })
            .catch((err) => {
                console.log(err);
            });
    },
};
</script>

<style scoped>
.articleListNew {
    /* position: relative; */
    /* background-color: #e8e8e8; */
    display: inline-block;
    vertical-align: top;
    /* background-color: rgb(229, 229, 225); */
    width: 70%;
    /* height:500px; */
    margin-left: 0%;
    /* padding-top: 0%; */
    /* margin-top:6% */
    /* 不设置高度，高度就自适应了 */
    /* max-height: 1400px; */
}

.latest {
    background-color: #e8e8e8;
    width: 96%;
    margin-bottom: 3%;
    margin-left: 2%;
    border-radius: 10px;
}

#latest_item {
    background-color: #f1f1f1;
    /* margin-top: 10px; */
    border-radius: 5px;
    height: 30px;
    margin: 5px 20px 0 15px;
}

#latest_item:hover {
    background-color: white;
}

#latest_item:hover #articleName{
    /* color:black; */
    font-weight: 500;
    color: #1890ff;
}

#category {
    font-size: 18px;

    margin-left: 3px;
    /* background-color:#e8e8e8; */
    background-color:#fcfcfc;

    border-radius: 3px;
    padding: 0 3px 0 3px;
}

#category:hover {
    background-color: rgb(255, 255, 255);
    color: #1890ff;
    font-weight: 550;

    /* color:black; */
}

#moreArticle {
    display: inline-block;
    background-color: #f1f1f1;
    border-radius: 3px;
    font-size: 18px;
    margin: 15px 0 20px 79%;
    padding: 5px 5px 5px 5px;
}

#moreArticle:hover {
    background-color: white;
}
</style>