<!-- 这个组件点击后，携带分类名信息，从获取到的所有文章信息中找出相应的文章信息，
    并在 articlelist 中展示出来 -->
<template>
    <div class="category">
        <div style="margin-left: 5%; margin-right: 5%; padding-top: 5%">
            <div display="inline-block" style="background-color:#f1f1f1">
                <!-- <icon-font type="icon-twitter" /> -->
                <h5 style="color: dimgray;margin:5px 0 5px 0" class="el-icon-folder-opened" display="inline-block">
                    分类
                </h5>
            </div>
            <hr style="margin-top: 0;" />

            <div class="content">
                <dl>
                    <dt v-for="item in arr" :key="item" style=";margin-top: 2px;">
                        <a id="category" @click="jump(item.name)" style="font-size: 16px;border-radius: 5px">

                            <div id="character" style="">
                                {{ item.name }}
                            </div>

                            <div style="display: inline-block;width:10%;height:80%;border-radius: 50%;color:dimgray">
                                {{ item.number }}
                            </div>

                        </a>
                    </dt>
                </dl>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: "categoryComponent",
    data() {
        return {
            length: "",
            arr: [],
        }
    },
    methods: {
        jump(name) {
            // console.log("点击事件没问题",name)
         

            this.$router.push({
                name: "articleListCate",
        //         query:{
        //   name,
        // }
            });//这个时候articleListCate组件实例创建了吗 ?
            // this.$bus.$emit('cateEvent', name);  //被触发的那个组件还没被创建出来?
            //这个路由组件被创建出来后,再次点击jump,不会再次创建。
        
            //临时解决办法: 对操作后的dom进行渲染?
            setTimeout(()=>{
                this.$bus.$emit('cateEvent', name);  //被触发的那个组件还没被创建出来?
            },200);
           
 
        }

    },
    created() {
        axios({
            method: "get",
            url: "http://43.139.56.159:3001/category/getall",
        })
            .then((res) => {
                this.length = res.data.data.length;
                for (var i = 0; i < this.length; i++) {
                    this.arr.unshift(res.data.data[i]); //倒序输出
                }
                // console.log(this.arr);
            })
            .catch((err) => {
                console.log(err);
            })
    }

}
</script>

<style scoped>
.category {
    background-color: #e8e8e8;
    width: 95%;
    height: auto;
    margin-left: 2%;
    margin-top: 8%;
    border-radius: 10px;
    /* box-shadow: 0px 4px 10px 2px rgb(184, 183, 183); */

}

#category:hover {
    /* background-color:lightgray; */
    background-color: #ffffff;
}
#category:hover #character{
    /* color: #1890ff; */
    color:black;
    font-weight: 500;
}
.content {
    padding-bottom: 10px;
    margin-left: 3%;
}
#character{
    display: inline-block;width:85%;color:dimgray;
}


/* #character:hover{
    color:black;
} */
</style>